html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

:root {
  --green-500: hsl(158, 36%, 37%);
  --green-700: hsl(158, 42%, 18%);
  --black: hsl(212, 21%, 14%);
  --grey: hsl(228, 12%, 48%);
  --cream: hsl(30, 38%, 92%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-montserrat: "Montserrat", serif;
  --montserrat-500: 500;
  --montserrat-700: 700;
  --font-fraunces: "Fraunces", serif;
  --fraunces-700: 700;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
}
@media (min-width: 40em) {
  .container {
    padding: 0;
    color: red;
  }
}

body {
  background-color: var(--cream);
}

.product-card {
  background-color: var(--white);
}

/* Prevent font size inflation */
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

/* Remove default margin in favour of better control in authored CSS */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin-block-end: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  line-height: 1.5;
}

/* Set shorter line heights on headings and interactive elements */
h1,
h2,
h3,
h4,
button,
input,
label {
  line-height: 1.1;
}

/* Balance text wrapping on headings */
h1,
h2,
h3,
h4 {
  text-wrap: balance;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
}

/* Make sure textareas without a rows attribute are not tiny */
textarea:not([rows]) {
  min-height: 10em;
}

/* Anything that has been anchored to should have extra scroll margin */
:target {
  scroll-margin-block: 5ex;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.product-card {
  max-width: 500px;
  border-radius: 0.625rem;
  overflow: hidden;
}

.product-card__tag {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-500);
  text-transform: uppercase;
  font-size: 0.875rem;
  letter-spacing: 0.3125rem;
  color: var(--grey);
}

.product-card__title {
  font-family: var(--font-fraunces);
  font-weight: var(--fraunces-700);
  font-size: 1.75rem;
  color: var(--black);
}

.product-card__description {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-500);
  font-size: 1rem;
  color: var(--grey);
}

.product-card__discounted {
  font-family: var(--font-fraunces);
  font-weight: var(--fraunces-700);
  font-size: 1.75rem;
  color: var(--green-500);
}

.product-card__original {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-500);
  font-size: 0.875rem;
  color: var(--grey);
}

.product-card__button {
  font-family: var(--font-montserrat);
  font-weight: var(--montserrat-700);
  font-size: 1rem;
  color: var(--white);
  background-color: var(--green-500);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 0.625rem;
  border: none;
}

.product-card__button-icon {
  color: var(--white);
}

.product-card__content {
  display: grid;
  grid-template-rows: 1fr;
  gap: 1rem;
  padding: 1.25rem;
}

.product-card__price {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}